<style scoped>
    .page {
        margin-top: 10px;
    }
</style>
<template>
    <div>
        <RadioGroup class="type" @on-change="changeType" v-model="type" type="button">
            <Radio label="all">全部</Radio>
            <Radio label="0">待处理</Radio>
            <Radio label="1">处理中</Radio>
            <Radio label="2">完成</Radio>
            <Radio label="3">取消</Radio>
        </RadioGroup>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
    </div>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '工单号',
                        key: 'work_order_number'
                    },
                    {
                        title: '工单内容',
                        key: 'work_order_content'
                    },
                    {
                        title: '工单状态',
                        key: 'workstatus'
                    },
                    {
                        title: '工单类型',
                        key: 'worktype'
                    },
                    {
                        title: '处理部门',
                        key: 'department'
                    },
                    {
                       title: '业务类型',
                        key: 'business_type' 
                    }
                ],
                data: [
                ],
                page: 0,
                pageTotal: 11,
                allData: []
            }
        },
        methods: {
            changeType(type) {
                if(type=="all") {
                    this.getData();
                }else {
                    this.getData({
                        work_order_status: type
                    });
                }
            },
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
            show (data) {

                this.$Modal.info({
                    title: '详细信息',
                    content: ``
                })
            }
        },
        getData(param = {}) {
                this.data = [];
                this.allData = [];
                 axios.get("http://"+location.hostname+"/home/customer/show_work_order",{
                     params: param
                 }).then(res => {
                    if(res.data.code==1) {
                        this.allData = res.data.data;
                        this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                    } else {
                        console.error(res.data.msg);
                    }
                });
        },
        mounted() {
            this.getData();
            // axios.get("http://"+location.hostname+"/home/customer/show_work_order").then(data => {
            //     if(data.data.code==1) {
            //         this.allData = data.data.data;
            //         this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
            //     } else {
            //         console.error(data.data.msg);
            //     }
            //  });
        }
    }
</script>